.title{
    color: #3a3a3a;
    font-size: 24px;
    font-weight: bold;
    font-family: "Poppins", serif;
}

.description{
    color: #3a3a3a;
    font-size: 18px;
    font-family: "Poppins", serif;
}

.f-p{
    color: #5d5d5d;
    margin-top: 10px;
    font-size: 14px;
    font-weight: normal;
    text-align: end;
    font-family: "Poppins", serif;
    text-decoration: none;
}

.f-p:hover{
    color: #0099ff;
}

.nav-container{
    display: grid;
    justify-content: center;
}

.nav-container2{
    display: grid;
    justify-items: center;
}

.spacer {
    margin-top: 30px;
}
.spacer2 {
    margin-top: 24px;
}
.navBarSpacer {
    margin-top: 100px;
}

.input-field {
    padding: 8px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
}

.input-field:focus {
    border-color: #0099ff;
    stroke-width: 2px;
}

.progressBar{
    justify-self: center;
}