

.link-logo {
    margin-right: 10px;
}

.menu-active {
    border-bottom: 1px solid #565387;
}

.navbar a:hover{
    color: #fff !important;
}

.navbar
{
    background: #2b2b2b;
}

.nav-link {
    font-family: "Poppins", serif;
}

.navbar-brand-title {
    font-family: "Poppins", serif;
    text-decoration: none;
    color: white;
}

.nav-options{
    font-size: 15px;
}

.true {
    color: #0099ff !important; /* Set the desired color value */
}

.nav-link.active {
    color: red; /* Set the desired color value */
}
.title {
    color: white;
}

.nav_ul li {
    padding: 0 0.6rem;
}

.nav_ul a {
    font-size: 1.05rem;
    color: #000 !important;
    font-weight: 400;
    letter-spacing: 0.5px;
}

.btn1 {
    border: 0;
    padding: 0.5rem 1.8rem;
    color: #fff;
    background-color: #01193c;
}

.btn2 {
    border: 0;
    padding: 0.5rem 1.2rem;
    color: #fff;
    background-color: #f60838;
}

.footer {
    padding: 2rem 0;
    margin-top: 10rem;
}

.ft-1 h2 {
    font-family: "Poppins", serif;
    font-weight: 500;
}

.ft-1 span {
    color: #f60838;
}

.ft-1 p {
    padding: 0rem 5rem 0.8rem 0;
    font-weight: 500;
}

.footer-icons {
    display: flex;
}

.footer-icons i {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.4rem;
    background: #e1e1e1;
    color: #f60838;
    margin: 0 0.45rem;
    border-radius: 50%;
    width: 35px;
    height: 35px;
}


.footer-icons i:hover {
    background: #f60838;
    color: #fff;
    transition: 0.4s;
    cursor: pointer;
}

.footer-icons i:not(:hover) {
    transition: 0.4s;
}

.footer h5 {
    color: #f60838;
}

.ft-2 ul {
    list-style: none;
    padding-left: 0;
}

.ft-2 ul li {
    padding: 0.35rem 0;
    font-weight: 500;
}

.ft-2 ul a {
    text-decoration: none;
    color: #000;
    font-size: 1.06rem;
}

.ft-3 p {
    font-weight: 500;
    padding: 0.1rem 0;
    font-size: 1.06rem;
}

.ft-3 i {
    padding-right: 0.5rem;
}

.Last-footer {
    background: #2b2b2b;
    text-align: center;
    padding: 1rem 0;
    color: white;
}

.Last-footer p {
    margin-bottom: 0;
    font-size: 1rem;
    font-weight: 400;
}